import { setInterval } from 'timers';
export default {
  name: 'Twitter',
  props: {
    'twitterName': {
      default: 'AurelienLoyer',
      type: String
    },
    'width': {
      type: String,
      default: '30vw'
    }
  },
  mounted: function mounted() {
    if (window.twttr != undefined) {
      window.twttr.widgets.load();
    }

    setInterval(function () {
      window.twttr.widgets.load();
    }, 5000);
  }
};